import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useSearchParams } from 'react-router-dom'

import type { SelectChangeEvent } from '@mui/material'
import {
  Container,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  Switch,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import orders from 'src/data/SortingOrders'
import CLUB_BELONGING from 'src/data/belonging'
import CLUB_CATEGORIES, { type ClubCategories } from 'src/data/categories'
import { CLUB_SEARCH_MESSAGE } from 'src/data/messages'

import PageTitleWrapper from 'src/components/PageTitleWrapper'
import RecruitNoticePreview from 'src/components/RecruitNoticePreview'
import Searchbox from 'src/components/Searchbox'

const SELECT_COMPONENT_WIDTH = '160px'

const RecruitNoticePage = () => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))
  const [searchParams, setSearchParams] = useSearchParams()
  const [params, setParams] = useState({
    name: searchParams.get('name') ?? '',
    category: (searchParams.get('category') as ClubCategories | '') ?? '',
    belonging: searchParams.get('belonging') ?? '',
    page: searchParams.has('page') ? parseInt(searchParams.get('page') || '1') : null,
    order: searchParams.has('order') ? searchParams.get('order') : ''
  })
  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    const search = document.getElementById('search') as HTMLInputElement
    setParams(prev => ({ ...prev, name: search.value, page: null, order: '' }))
  }
  const handleChange = ({
    target: { name, value }
  }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    setParams(prev => ({ ...prev, category: name === 'type' ? '' : prev.category, page: null, [name]: value }))
  }
  const handleClickCategory: React.MouseEventHandler<HTMLButtonElement> = event => {
    const category = event.currentTarget.getAttribute('data-value') as ClubCategories

    if (!CLUB_CATEGORIES.includes(category)) {
      enqueueSnackbar(CLUB_SEARCH_MESSAGE.WRONG_CATEGORY, { variant: 'warning' })
      return
    }
    setParams(prev => ({ ...prev, category: category }))
  }
  const tempClubInfo = Array(5).fill({
    id: 0,
    name: 'Í∞ÄÏùÑÍ∑Ä',
    category: 'Ïπ¥ÌÖåÍ≥†Î¶¨',
    belonging: 'Ï†ïÎ≥¥ÎåÄÌïô',
    description:
      "Î¨∏ÌôîÏÉùÌôú Ïó∞Ìï©ÎèôÏïÑÎ¶¨ Í∞ÄÏùÑÍ∑ÄÏóêÏÑú 25Í∏∞ 'ÎÇ®ÏûêÎ∂ÄÏõê'ÏùÑ Ï∂îÍ∞Ä Î™®ÏßëÌï©ÎãàÎã§! üçÅÎ¨∏ÌôîÏÉùÌôú Ïó∞Ìï©ÎèôÏïÑÎ¶¨ Í∞ÄÏùÑÍ∑ÄÏóêÏÑú 25Í∏∞ 'ÎÇ®ÏûêÎ∂ÄÏõê'ÏùÑ Ï∂îÍ∞Ä Î™®ÏßëÌï©ÎãàÎã§!",
    img: '',
    daysLeft: 2,
    nLikes: '1972'
  })

  useEffect(() => {
    const newParams = Object.assign(
      {},
      params.name ? { name: params.name } : null,
      params.category ? { category: params.category } : null,
      params.belonging ? { type: params.belonging } : null,
      params.page ? { page: params.page } : null,
      params.order ? { order: params.order } : null
    )
    setSearchParams(newParams)
  }, [params])

  return (
    <>
      <Helmet>
        <title>Î™®Ïßë Í≥µÍ≥†</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Stack direction="row" alignItems="baseline">
              <Typography variant="h3" component="h3" gutterBottom sx={{ marginRight: 1 }}>
                ÌòÑÏû¨ Î™®Ïßë Ï§ë ÎèôÏïÑÎ¶¨
              </Typography>
            </Stack>
            <Typography variant="subtitle2">ÌÇ§ÏõåÎìúÏôÄ Ïπ¥ÌÖåÍ≥†Î¶¨, Î∂ÑÎ•òÎ•º ÌôúÏö©Ìï¥ ÏõêÌïòÎäî ÎèôÏïÑÎ¶¨Î•º Ï∞æÏïÑÎ≥¥ÏÑ∏Ïöî</Typography>
          </Grid>
        </Grid>
        <Searchbox
          handleSubmit={handleSubmit}
          searchDefaultValue={params.name}
          filterCategory={params.category}
          handleClickCategory={handleClickCategory}
        />
      </PageTitleWrapper>
      <Container sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <Grid container width="100%" direction={isNotMobile ? 'row' : 'column'} spacing={1}>
          <Stack direction="row" alignItems="center" justifyContent="end">
            <FormControl size="small">
              <FormControlLabel
                control={<Switch color="primary" />}
                label="ÏßÄÏõê Í∞ÄÎä• Í≥µÍ≥†Îßå Î≥¥Í∏∞"
                labelPlacement="start"
              />
            </FormControl>
            <FormControl size="small">
              <FormControlLabel control={<Switch color="primary" />} label="ÏÉÅÏãúÎ™®ÏßëÍ≥µÍ≥† Î≥¥Í∏∞" labelPlacement="start" />
            </FormControl>
          </Stack>
          <Grid item sx={{ marginLeft: isNotMobile ? 'auto' : 0 }}>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="belonging-label">ÏÜåÏÜç</InputLabel>
              <Select
                labelId="belonging-label"
                id="belonging"
                name="belonging"
                label="ÏÜåÏÜç"
                value={params.belonging}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">ÏÑ†ÌÉùÌïòÏßÄ ÏïäÏùå</MenuItem>
                {CLUB_BELONGING &&
                  CLUB_BELONGING.map((name, idx) => (
                    <MenuItem key={idx} value={name}>
                      {name}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
          <Grid item>
            <FormControl size="small" fullWidth={!isNotMobile}>
              <InputLabel id="order-label">Ï†ïÎ†¨</InputLabel>
              <Select
                labelId="order-label"
                id="order"
                name="order"
                label="Ï†ïÎ†¨"
                value={params.order ?? ''}
                onChange={handleChange}
                sx={{ minWidth: SELECT_COMPONENT_WIDTH }}
              >
                <MenuItem value="">ÏÑ†ÌÉùÌïòÏßÄ ÏïäÏùå</MenuItem>
                {orders &&
                  Object.entries(orders).map(([key, value]) => (
                    <MenuItem key={key} value={key}>
                      {value}
                    </MenuItem>
                  ))}
              </Select>
            </FormControl>
          </Grid>
        </Grid>
        <Grid container spacing={2} marginTop={1}>
          {tempClubInfo.map(club => (
            <RecruitNoticePreview {...club} />
          ))}
        </Grid>
      </Container>
    </>
  )
}

export default RecruitNoticePage
