import FavoriteIcon from '@mui/icons-material/Favorite'
import { Box, Card, CardActionArea, Chip, Grid, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { type ClubBelonging } from 'src/data/belonging'

import { stringToColor } from 'src/utils'

export interface RecruitNoticePreviewProps {
  id: number
  name: string
  category: string
  belonging: ClubBelonging
  description: string
  img: string
  daysLeft: number
  nLikes: number
}

const RecruitNoticePreview = ({
  id,
  name,
  category,
  belonging,
  description,
  img,
  daysLeft,
  nLikes
}: RecruitNoticePreviewProps) => {
  const theme = useTheme()
  const isNotMobile = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Grid item xs={12} sm={12} md={6}>
      <Card sx={{ height: '100%' }}>
        <CardActionArea sx={{ display: 'flex', alignItems: 'stretch', padding: theme.spacing(2), height: '100%' }}>
          <Stack direction="column" flex="1" spacing={1}>
            <Stack direction="row" spacing={1}>
              <Chip
                label={category}
                size="small"
                sx={{ padding: theme.spacing(0.5), fontSize: isNotMobile ? 13 : 11 }}
              />
              {belonging && (
                <Chip
                  label={belonging}
                  size="small"
                  sx={{ padding: theme.spacing(0.5), fontSize: isNotMobile ? 13 : 11 }}
                />
              )}
            </Stack>
            <Stack direction="row" spacing={1} alignItems="center" paddingLeft={0.5} paddingTop={0.5}>
              {/* <Box sx={{ backgroundColor: '#008989' }} width="28px" height="28px" borderRadius="50%" /> */}
              <Typography variant="h4">{name}</Typography>
            </Stack>
            <Typography
              paddingLeft={0.4}
              paddingTop={0.2}
              paddingRight={1.5}
              variant="body2"
              fontSize={15}
              flex={1}
              overflow="hidden"
              textOverflow="ellipsis"
              sx={{
                wordBreak: 'keep-all',
                display: '-webkit-box',
                '-webkit-line-clamp': '2',
                '-webkit-box-orient': 'vertical'
              }}
            >
              {description}
            </Typography>
            <Stack direction="row" alignItems="center" paddingLeft={0.5}>
              <Typography variant="body2" fontSize={15} color={theme.colors.primary.main} fontWeight="bold">
                {`D-${daysLeft}`}
              </Typography>

              <FavoriteIcon
                sx={{
                  width: '0.6em',
                  height: '0.6em',
                  marginLeft: theme.spacing(1),
                  color: theme.colors.alpha.black[30]
                }}
              />
              <Typography variant="body2" fontSize={15} color={theme.colors.alpha.black[30]} marginLeft={0.2}>
                {nLikes}
              </Typography>
            </Stack>
          </Stack>
          <Box display="flex" alignItems="center" width="130px">
            <Box width="130px" height="130px" sx={{ backgroundColor: stringToColor(name, 0.3) }} />
          </Box>
        </CardActionArea>
      </Card>
    </Grid>
  )
}

export default RecruitNoticePreview
